.TodoItem {
    background-color: #EFB357;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;   
    border-radius: 10px;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  }
  
  .TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 22px;
    line-height: 24px;
    font-weight: 400;
    color: aliceblue;
    font-style: italic;
  }

  .TodoItem-p--complete {
    text-decoration: line-through;
  }
  
  .Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    /* background-color: #CCC; */
  }
  
