  .LoadingTodo-container {
    position: relative;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);    
  }

  .LoadingTodo-text {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 22px;
    line-height: 24px;
    font-weight: 400;
    color: aliceblue;
    font-style: italic;
  }

  .LoadingTodo-deleteIcon,
  .LoadingTodo-completeIcon{
    border-radius: 50px;
    height: 48px;
    width: 48px;
  }

  .LoadingTodo-deleteIcon {
    position: absolute;
    top: -24px;
    right: 0; 
    color: #DD6767;   
  }
  
  .LoadingTodo-completeIcon {
    position: absolute;
    left: 12px;
  }

  .LoadingTodo-container,
  .LoadingTodo-completeIcon,
  .LoadingTodo-deleteIcon{
    background: linear-gradient(90deg, rgb(239, 179, 87), rgb(238, 218, 157));
    background-size: 400% 400%;
    animation: loadingAnimation 3s ease-in-out infinite;
  }

  @keyframes loadingAnimation{
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }

  
  
  